import { Component,OnInit } from '@angular/core';
import {} from 'textillate';
import { from } from 'rxjs';
import * as $ from 'jquery';
const url1 = 'https://cdnjs.cloudflare.com/ajax/libs/animejs/2.0.2/anime.min.js'
const url2 = 'assets/js/desc.js'
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: []
})
export class AppComponent implements OnInit{
  title = 'app';
  public loadScript(url) {
    let body = <HTMLDivElement> document.body;
    let script = document.createElement('script');
    script.innerHTML = '';
    script.src = url;
    script.async = true;
    script.defer = true;
    body.appendChild(script);
}
  ngOnInit() {
    // this.loadScript(url1)
    this.loadScript(url2)

    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    setTimeout(() => {
      document.getElementById('desc').innerHTML = 'A new kind of Web-portfolio Xperience!'
    }, 7000);  
    
  }  
}
